body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-nav{
    flex-direction: row;
    margin-left: 50px;
}

.root-div {
    /*min-height: 667px;*/
    height: 100vh;
    width: 100vw;
    display: block;
    position: relative;
    color: teal;
    /*color: white;*/
    padding: 20px;
}
.root-div {
    content: '';
    background-image: url(/static/media/background.58ef49e9.png);
    /*background-image: url('./couple1.jpg');*/
    /*background-image: url('./couple2.png');*/
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /*opacity: 0.66;*/
    opacity: 1;
    z-index: -1;
}

.navbar {
    margin: 0;
    padding: 0;
    width: 100%;
}
